import { Stuff, properties } from "./Stuff";
import { cm1, geo, mat, sounds } from "./modules/common";
import { Mesh } from "three";

interface GlassProps extends properties{
    type:string,
    step:number,
}

export class Glass extends Stuff{
    geometry:any;
    material:any;
    mesh:any;
    type:string;
    step:number;
    // width:number;
    // height:number;
    // depth:number;

    constructor(props:GlassProps){
        super(props);

        this.type = props.type;
        this.step = props.step;
        const sound = sounds[this.type==='normal' ? 'normal':'strong']

        this.geometry = geo.glass;
        // this.material = mat.glass;
        switch(props.type){
            case 'normal':
                this.material = mat.glass1;
                break;
            case 'strong':
                this.material = mat.glass2;
                break;
        }
        this.width = this.geometry.parameters.width;
        this.height = this.geometry.parameters.height;
        this.depth = this.geometry.parameters.depth;

        this.mesh = new Mesh(this.geometry, this.material);
        this.mesh.position.set(this.x, this.y, this.z);
        this.mesh.castShadow = true;
        this.mesh.receiveShadow = true;
        this.mesh.name = this.name;
        this.mesh.step = this.step;
        this.mesh.type = this.type;
        this.mass = this.type === 'normal' ? 1 : 0;

        cm1.scene.add(this.mesh);
        this.setCannonBody()

        this.cannonBody.addEventListener('collide', playSound);
        function playSound(e:any){
            const strength = e.contact.getImpactVelocityAlongNormal();
            if(strength > 6){
                sound.currentTime = 0;
                sound.play();
            }
        }
    }
}