import { Stuff, properties } from "./Stuff";
import { cm1 } from "./modules/common";
import { AnimationMixer, BoxGeometry, Mesh, MeshBasicMaterial } from "three";

interface PlayerProps extends properties{
}

export class Player extends Stuff{
    geometry:any;
    material:any;
    modelMesh:any;
    actions:any[]=[];
    // width:number;
    // height:number;
    // depth:number;

    constructor(props:PlayerProps){
        super(props);

        this.width = 0.5;
        this.height = 0.5;
        this.depth = 0.5;

        cm1.gltfLoader.load(
            `${process.env.PUBLIC_URL}/models/ilbuni.glb`,
            (glb:any) =>{
                // shadow
                glb.scene.traverse((child:any) =>{
                    if(child.isMesh){
                        child.castShadow = true;
                    }
                })

                this.modelMesh = glb.scene.children[0];
                this.modelMesh.position.set(this.x, this.y, this.z);
                this.modelMesh.rotation.set(this.rotationX, this.rotationY, this.rotationZ);
                this.modelMesh.castShadow = true;

                this.modelMesh.animations = glb.animations;
                cm1.mixer = new AnimationMixer(this.modelMesh);
                this.actions = [];
                this.actions[0] = cm1.mixer.clipAction(this.modelMesh.animations[0])
                this.actions[1] = cm1.mixer.clipAction(this.modelMesh.animations[1])
                this.actions[2] = cm1.mixer.clipAction(this.modelMesh.animations[2])
                this.actions[2].repetitions = 1;

                this.actions[0].play();

                cm1.scene.add(this.modelMesh)
            }
        )
        this.setCannonBody()
    }
}