import { Body, Box, Vec3 } from "cannon-es";
import { cm1 } from "./modules/common";

export interface properties {
    name:string,
    x?:number,
    y?:number,
    z?:number,
    rotationX?:number,
    rotationY?:number,
    rotationZ?:number,
    cannonMaterial?:any,
    width?:number;
    height?:number;
    depth?:number;
    mass?:number;
}

export class Stuff{
    name:string;
    x:number;
    y:number;
    z:number;
    rotationX:number;
    rotationY:number;
    rotationZ:number;
    cannonMaterial:any;
    width:number;
    height:number;
    depth:number;
    mass:number;
    cannonBody:any;

    constructor(props={} as properties){
        this.name = props.name || '';
        this.x = props.x || 0;
        this.y = props.y || 0;
        this.z = props.z || 0;

        this.rotationX = props.rotationX || 0;
        this.rotationY = props.rotationY || 0;
        this.rotationZ = props.rotationZ || 0;

        this.width = 0;
        this.height =  0;
        this.depth =  0;

        this.mass = props.mass || 0;

        this.cannonMaterial = props.cannonMaterial || cm1.defaultMaterial;
    }

    setCannonBody(){
        const material = this.cannonMaterial;
        const shape = new Box(new Vec3(this.width/2, this.height/2, this.depth/2));

        this.cannonBody = new Body({
            mass: this.mass,
            position: new Vec3(this.x, this.y, this.z),
            shape,
            material,
        })
        this.cannonBody.quaternion.setFromAxisAngle(
            new Vec3(0, 1, 0),
            this.rotationY
        )
        cm1.world.addBody(this.cannonBody);
    }
}