import { Stuff, properties } from "./Stuff";
import { cm1, cm2, geo, mat } from "./modules/common";
import { Mesh } from "three";

interface lightProps extends properties{
    container:any,
}

export class SideLight{
    geometry:any;
    material:any;
    mesh:any;
    name:string;
    x:number;
    y:number;
    z:number;

    constructor(props:lightProps){

        const container = props.container || cm1.scene;
        this.name = props.name || ''
        this.x = props.x || 0;
        this.y = props.y || 0;
        this.z = props.z || 0;

        this.geometry = geo.sightLight;
        this.material = mat.sightLight;

        this.mesh = new Mesh(this.geometry, this.material);
        this.mesh.position.set(this.x, this.y, this.z);

        container.add(this.mesh);
    }

    turnOff(){
        this.mesh.material.color.set(cm2.lightOffColor)
    }
}